.link-fx-nav-default {
  background: none;
  color: inherit;
  cursor: pointer;
  text-decoration: none;
  -webkit-transition: color 500ms;
  -o-transition: color 500ms;
  transition: color 500ms;
}
.link-fx-nav-default:hover,
.link-fx-nav-default:focus {
  background: none;
  color: #26cad3;
  text-decoration: none;
}
.link-fx-nav-default:active,
.link-fx-nav-default.active {
  color: #95d600;
  -webkit-transition: color 0s;
  -o-transition: color 0s;
  transition: color 0s;
}
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.SandboxRoot .timeline-Tweet-text {
  font-size: 1em !important;
  line-height: 1.5em !important;
}
body {
  font-size: 16px;
}
body > .timeline-Widget {
  width: 100%;
  max-width: none;
  border-radius: 0;
}
.timeline-Widget > .timeline-Header {
  border-top: 1px solid rgba(15, 70, 100, 0.12);
}
.timeline-Widget > .timeline-Header,
.timeline-Widget > .timeline-Footer {
  padding: 1em 40px;
}
.timeline-TweetList {
  margin: 0;
  padding: 0;
  list-style: none;
}
.timeline-TweetList > .timeline-TweetList-tweet {
  width: 100%;
  display: block;
  float: left;
  position: relative;
  margin: 0;
  padding: 0;
  padding-bottom: 50%;
  overflow: hidden;
  border: 0 none;
  color: inherit;
}
@media screen and (min-width: 800px) and (max-width: 1199px) {
  .timeline-TweetList > .timeline-TweetList-tweet {
    width: 50%;
    padding-bottom: 25%;
  }
}
@media screen and (min-width: 1200px) {
  .timeline-TweetList > .timeline-TweetList-tweet {
    width: 33.33333333%;
    padding-bottom: 16.66666667%;
  }
}
.timeline-TweetList > .timeline-TweetList-tweet > .timeline-Tweet {
  width: 100%;
  height: 100%;
