}
.timeline-Tweet > .timeline-Tweet-media .MediaCard-borderOverlay {
  border: 0 none;
}
.timeline-Tweet > .timeline-Tweet-media .MediaCard-borderOverlay > .u-hiddenVisually {
  width: 100% !important;
  height: 100% !important;
  clip: initial !important;
}
.timeline-Tweet > .timeline-Tweet-media .TwitterCard {
  display: none;
}
.timeline-Tweet > .timeline-Tweet-actions,
.timeline-Tweet > .timeline-Tweet-metadata {
